.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
}

.Icon-container-check {
    position: absolute;
    left: 12px;
}
.Icon-container-check--active {
    color:  rgb(86, 239, 39);
}

.Icon-container-delete {
    position: relative;
    top: -24px;
    right: 0;
}


.Icon-svg {
    width: 24px;
    height: 24px;  
}

.Icon-container-check:hover .Icon-svg {
    fill: rgb(86, 239, 39);
}

.Icon-container-delete:hover .Icon-svg {
    fill: red;
}

